@use "@material/icon-button";
@use "@material/chips/mdc-chips";
@import "/src/styles/App.scss";

@include icon-button.core-styles;

.material-icons.md-spec { font-size: 120px; }

.home {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.name {
    font-size: 2.5em;
    padding-top: 1.5em;
    padding-bottom: 1.25em;
    font-family: 'Roboto', sans-serif;;
}

.dailyGoalsText {
    font-size: 1.5em;
    text-decoration: underline;
    font-family: 'Roboto', sans-serif;;
}

.middleGraphs {
    padding-top: 5em;
}
.pie {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.chip-selected {
    background-color: rgba($color: $blue, $alpha: .3) !important;
}

.mdc-icon-button {
    @include icon-button.size(15);
}

.weeklyGoalGraphs {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.bottomGraphs {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    padding-top: 7.5em;
}

.bottomGraph {
    padding-left: 3em;
    padding-right: 3em;
}